@import './variables.scss';

html {
  overflow: hidden;
}

.App {
  text-align: center;

  .fireworks-container {
    position: absolute;
    width: 100vw;
    height: 100vh;
  }

  .App-content {
    background-color: $background-color;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    color: white;

    .play-again {
      color: palegreen;
      border: 5px solid palegreen;
      padding: 16px;
      outline: 0;
      z-index: 100;
      font-size: 32pt;
      font-weight: bolder;
      background-color: $background-color;
      text-transform: uppercase;
    }
  }
}