.length-select {
  display: flex;
  flex-direction: row;
  height: 50px;
  justify-items: center;
  align-items: center;
  column-gap: 5px;
  z-index: 100;

  .button {
    flex-grow: 1;
    width: 50px;
    font-size: 30pt;
    height: 100%;
    color: #bbb;
    background-color: transparent;
    border: 0;
    outline: 0;
    cursor: pointer;
  }

  p {
    font-weight: bold;
  }
}