@import '../variables.scss';

/* The flip card container - set the width and height to whatever you want. We have added the border property to demonstrate that the flip itself goes out of the box on hover (remove perspective if you don't want the 3D effect */
.flip-letter {
  background-color: transparent;
  width: $letter-edge-size;
  height: $letter-edge-size;
  font-size: $letter-font-size;
  perspective: 1000px; /* Remove this if you don't want the 3D effect */
  cursor: pointer;

  /* This container is needed to position the front and back side */
  .flip-letter-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.8s;
    transform-style: preserve-3d;

    /* Do an horizontal flip  */
    &.flip-letter-flipped {
      transform: rotateY(180deg);
    }
  }

  /* Style the front side (fallback if image is missing) */
  .flip-letter-front {
    box-sizing: border-box;
    border: $letter-border-size solid #bbb;
    border-radius: 10px;
    background-color: #282c34;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    &.blank {
      color: #bbb;
      background-color: #bbb;
    }
  }

  /* Style the back side */
  .flip-letter-back {
    color: white;
    transform: rotateY(180deg);
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;

    &.wrong {
      color: #282c34;
      background-color: palevioletred;
    }

    &.position {
      // styles for when the letter is in incorrect position
      color: #282c34;
      background-color: goldenrod;
    }

    &.correct {
      // styles for when the letter is in correct position
      color: #282c34;
      background-color: palegreen;
    }
  }

  /* Position the front and back side */
  .flip-letter-front, .flip-letter-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden; /* Safari */
    backface-visibility: hidden;
  }
}

.pulse {
  animation: pulse-animation 0.5s infinite;
}

@keyframes pulse-animation {
  0% {
    box-shadow: 0 0 0 0px rgba(255, 255, 255, 0.2);
  }
  100% {
    box-shadow: 0 0 0 10px rgba(255, 255, 255, .8);
  }
}
