.keyboard {
  display: flex;
  flex-direction: column;
  // border: 2px solid white;
  padding: 10px;
  // border-radius: 10px;
  // background-color: #bbb;

  .keyboard-row {
    display: flex;
    flex-direction: row;
    column-gap: 10px;
    margin-bottom: 10px;

    .flip-letter {
      font-size: 20pt;
      // background-color: gray;

      .wrong {
        color: #bbb;
        background-color: #bbb;
      }
    }
    .flip-letter-front {
      border-radius: 0px;
    }
  }
}

@media screen and (max-width: 450px)  {
  .keyboard {
    padding: 5px;

    .keyboard-row {

      .flip-letter {

        .flip-letter-front {

          font-weight: bold;
        }

        width: 30px;
        height: 30px;
        font-size: 13pt;
      }
    }

  }
}
